<template>
  <div class="sidebars">
    <div class="sidebar">
      <div v-for="(item, index) in sidebar" :key="index">
        <div
          :class=" ins == '00' && color == index ? 'bs' : 'normal'"
          @click="selectitems(index)"
        >
          <img :src="item.img" alt="" class="icon" />
          <div class="title">{{ item.title }}</div>
          <img
            v-show="item.flag == false"
            :src="item.sanjiao"
            alt=""
            class="sanjiao"
          />
          <img
            v-show="item.flag == true"
            :src="item.top"
            alt=""
            class="sanjiao"
          />
        </div>
        <div
          :class="[
            item.flag ? 'select-item' : 'select-no',
            ins == index + '' + indexs ? 'bs' : 'select-item',
          ]"
          :data-aa="index + '' + indexs"
          v-for="(items, indexs) in item.item"
          :key="indexs"
          @click="colors(indexs, $event)"
        >
          {{ items.topic }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import bus from '@/bus'
export default {
  data() {
    return {
      indexflag: 0,
      show: null,
      shows: false,
      user: false,
      color: "",
      sidebar: [
        {
          color: true,
          id: 0,
          img: require("../assets/homes.png"),
          title: "首页",
        },
        {
          color: false,
          id: 1,
          flag: false,

          img: require("@/assets/KTV.png"),
          title: "KTV管理",
          sanjiao: require("@/assets/sj.png"),
          top: require("@/assets/top.png"),
          item: [{ topic: "KTV列表" }, { topic: "人员列表" }],
        },
        {
          color: false,
          id: 2,
          flag: false,
          img: require("@/assets/juben.png"),
          title: "剧本管理",
          sanjiao: require("@/assets/sj.png"),
          top: require("@/assets/top.png"),
          item: [{ topic: "剧本列表" }, { topic: "房间列表" }],
        },
        {
          color: false,
          id: 3,
          flag: false,
          img: require("@/assets/user.png"),
          title: "用户管理",
          sanjiao: require("@/assets/sj.png"),
          top: require("@/assets/top.png"),
          item: [{ topic: "用户列表" }, { topic: "房间列表" }],
        },
        {
          color: false,
          id: 4,
          flag: false,
          img: require("@/assets/address.png"),
          title: "区域管理",
          sanjiao: require("@/assets/sj.png"),
          top: require("@/assets/top.png"),
          item: [{ topic: "KTV列表" }, { topic: "房间列表" }],
        },
        {
          color: false,
          id: 5,
          flag: false,
          img: require("@/assets/order.png"),
          title: "订单管理",
          sanjiao: require("@/assets/sj.png"),
          top: require("@/assets/top.png"),
          item: [{ topic: "KTV列表" }, { topic: "房间列表" }],
        },
        {
          color: false,
          id: 6,
          flag: false,
          img: require("@/assets/fenlei.png"),
          title: "分类管理",
          sanjiao: require("@/assets/sj.png"),
          top: require("@/assets/top.png"),
          item: [{ topic: "KTV列表" }, { topic: "房间列表" }],
        },
        {
          color: false,
          id: 7,
          flag: false,
          img: require("@/assets/guanggao.png"),
          title: "广告管理",
          sanjiao: require("@/assets/sj.png"),
          top: require("@/assets/top.png"),
          item: [{ topic: "KTV列表" }, { topic: "房间列表" }],
        },
        {
          color: false,
          id: 8,
          flag: false,
          img: require("@/assets/sucai.png"),
          title: "素材管理",
          sanjiao: require("@/assets/sj.png"),
          top: require("@/assets/top.png"),
          item: [{ topic: "KTV列表" }, { topic: "房间列表" }],
        },
        {
          color: false,
          id: 9,
          flag: false,
          img: require("@/assets/admin.png"),
          title: "管理员管理",
          sanjiao: require("@/assets/sj.png"),
          top: require("@/assets/top.png"),
          item: [{ topic: "KTV列表" }, { topic: "房间列表" }],
        },
        {
          color: false,
          id: 10,
          flag: false,
          img: require("@/assets/tongzhi.png"),
          title: "通知管理",
          sanjiao: require("@/assets/sj.png"),
          top: require("@/assets/top.png"),
          item: [{ topic: "KTV列表" }, { topic: "房间列表" }],
        },
      ],
      // index: 0
      ins: "00",
      ishow: 0,
      indexs:0
    };
  },
  methods: {
    selectitems(e) {
      // 点击color = index
      this.color = e;
      // 如果点击的index =0，即点第一个首页
      // ins = 00 ，当ins=00时，背景色显示
      // 反之，当index ！=0时，如果ins不是00（点击下拉框改变ins（10，11等等），那么ins不变，反之让ins=00
      if (this.color == 0) {
        this.ins = "00";
        this.ishow = 0;
      } else {
        if (this.ins != "00") {
          this.ins = this.ins;
        } else {
          this.ins = "00";
        }
        this.color = 0;
        this.ishow = 1;
      }
      // console.log(e);
      this.indexs = e
      // 如果ins =00并且ishow=1，就跳转到当前页面，反之ishow=0跳转回home页
      if (this.ins == "00") {
        if (this.ishow == 1) {
          this.$router.push(this.$route.path);
        } else {
          this.$router.push("/home");
        }
      }
      this.sidebar[e].flag = !this.sidebar[e].flag;
    },
    colors(e, ev) {
      console.log(e);
      // this.sidebar[this.indexflag].item[e].color =
      //   !this.sidebar[this.indexflag].item[e].color;
      // 自定义属性，让点击的index = 00，01，10，11这样，这样可以让循环之间互不干扰
      let ins = ev.target.dataset.aa;
      this.ins = ins;
      // console.log(this.ins);
      this.colorer = e;
      switch (this.ins) {
        case "00":
          this.$router.push("/home");
          break;
        case "10":
          if (this.$route.path != "/ktvlist") {
            this.$router.push("/ktvlist");
          } else {
            this.$router.push("/ktvlist");
          }
          break;
        case "11":
          if (this.$route.path != "/employee") {
            this.$router.push("/employee");
          } else {
            this.$router.push("/employee");
          }
          break;
        default:
      }
      let x = this.ins.split('')[0]
      console.log(this.sidebar[x].item[e].topic);
    },
  },
  mounted() {
    if (this.ins == "00") {
      this.$router.push("/home");
    }
    this.ins = sessionStorage.getItem('ins')
    this.color = sessionStorage.getItem('color')
  },
  create(){

  },
  watch:{
  }
};
</script>

<style scoped>
.sidebar {
  width: 310px;
  background: #171c1f;
  display: flex;
  flex-direction: column;
