}
.sidebar .normal {
  width: 310px;
  height: 68px;
  background: rgb(23, 28, 31);
  position: relative;
  cursor: pointer;
}
.sidebar .bs {
  width: 310px;
  height: 68px;
  background: linear-gradient(to right, rgb(229, 148, 128), rgb(85, 57, 212));
  position: relative;
  border-radius: 0px 20px 0px 0px;
  cursor: pointer;
}
.select-item {
  font-size: 20px;
  height: 68px;
  line-height: 68px;
  font-family: PingFang SC-Medium, PingFang SC;
  font-weight: lighter;
  background-color: rgb(31, 33, 34);
  color: #f4f0e9;
  position: relative;
  transition: all 0.4s;
  animation: dh 0.5s linear;
  cursor: pointer;
}
.select-no {
  display: none;
}
.sidebar .icon {
  width: 30px;
  height: 30px;
  position: absolute;
  top: 19px;
  left: 21px;
}
.sidebar .title {
  font-size: 20px;
  font-family: PingFang SC-Medium, PingFang SC;
  font-weight: 500;
  color: #f4f0e9;
  position: absolute;
  top: 20px;
  left: 115px;
}
.sidebar .sanjiao {
  position: absolute;
  top: 28px;
  right: 25px;
  border: none;
  cursor: pointer;
}
@keyframes dh {
  from {
    opacity: 0;
    height: 0;
  }
  to {
    opacity: 1;
    height: 68px;
  }
}
</style>