import Vue from 'vue'
import VueRouter from 'vue-router'
import layout from '../layout/layout.vue'

Vue.use(VueRouter)
const originalPush = VueRouter.prototype.push
//修改原型对象中的push方法
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

const routes = [
  {
    path: '/',
    name: '布局页',
    component: layout,
    redirect: '/login',
    children: [
      {
        path: '/ktvlist',
        name: 'KTV列表页',
        meta: {
          tabInfo: {
            component: 'ktvlist',
            title: 'KTV列表',
            path: '/ktvlist'
          }
        },
        component: () => import('../views/KTVlist.vue')
      },
      {
        path: '/home',
        name: 'home',
        meta: {
          tabInfo: {
            component: 'Home',
            title: '首页',
            path: '/home'
          }
        },
        component: () => import('../views/home.vue')
      },
      {
        path: '/user',
        name: '个人中心页',
        component: () => import('../views/user.vue')
      },
      {
        path: '/employee',
        name: '员工列表',
        meta: {
          tabInfo: {
            component: 'employee',
            title: '员工列表',
            path: '/employee'
          }
        },
        component: () => import('../views/employeeslist.vue')
      },
      {
        path: '/notice',
        name: '通知列表',
        meta: {
          tabInfo: {
            component: 'notice',
            title: '通知列表',
            path: '/notice'
          }
        },
        component: () => import('../views/notice.vue')
      },
      {
        path: '/sucai',
        name: '素材列表',
        meta: {
          tabInfo: {
            component: 'sucai',
            title: '素材列表',
            path: '/sucai'
          }
        },
        component: () => import('../views/sucai.vue')
      },
      {
        path: '/juben',
        name: '剧本列表',
        meta: {
          tabInfo: {
            component: 'juben',
            title: '剧本列表',
            path: '/juben'
          }
        },
        component: () => import('../views/juben.vue')
      },
      {
        path: '/fenlei',
        name: '分类列表',
        meta: {
          tabInfo: {
            component: 'fenlei',
            title: '分类列表',
            path: '/fenlei'
          }
        },
        component: () => import('../views/fenlei.vue')
      },
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('../views/login/login.vue')
  },
  {
    path: '/tuzi',
    name: 'tuzi',
    component: () => import('../views/tuzi.vue')
  },
  {
    path: '/tuzihome',
    name: 'tuzihome',
    component: () => import('../views/tuzihome.vue')
  },
  {
    path: '/pie',
    name: 'tuzihome',
    component: () => import('../views/bing.vue')
  },
  {
    path: '/car',
    name: 'car',
    component: () => import('../views/car.vue')
  },
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes,
})
// const VueRouterPush = router.prototype.push
// router.prototype.push = function push(to) {
//   return VueRouterPush.call(this, to).catch(err => err)
// }
router.beforeEach((to, from, next) => {
  const token = localStorage.getItem('token')
  if (to.path != '/login' && !token) {
    alert('请先登录'),
      next('/login')
  } else if (to.path == '/login' && token) {
    next('/home')
  } else {
    next()
  }
})

export default router
