<template>
  <div class="navd">
    <div class="navs">
      <h3>剧本杀后台管理系统</h3>
      <div class="user">
        <img
          src="@/assets/jay1.jpg"
          alt=""
          class="avatar"
          @mouseover="usere"
          @mouseout="userout"
          @click="userinfo"
        />
        <span class="name">{{ nickname }}</span>
        <img src="@/assets/sanjiao.png" alt="" class="users" @click="users" />
      </div>
      <div :class="user ? 'userinfo' : 'userinfos'" @click.stop="!user">
        <div class="tops">
          <img src="@/assets/jay1.jpg" alt="" class="avatars" />
          <div class="names">{{ nickname }}</div>
        </div>
        <div class="exit" @click="exit">退出登录</div>
      </div>
    </div>
  </div>
</template>

<script>
import { loginout } from "@/api/login";
import { user } from "@/api/user";
export default {
  data() {
    return {
      user: false,
      avatar: "",
      nickname: "",
    };
  },
  methods: {
    usere() {
      this.user = !this.user;
    },
    userout() {
      this.user = !this.user;
    },
    users() {
      this.user = !this.user;
    },
    userinfo() {
      // userinfo({params:''}).then((res)=>{
      //   console.log(res);
      // })
    },
    exit() {
      loginout().then((res) => {
        if (res.code == 200) {
          localStorage.removeItem("token");
          this.$router.push("/login");
        } else {
          alert("退出失败");
        }
      });
      // this.$router.push('/login')
    },
  },
  mounted() {
    user().then((res) => {
      this.avatar = res.user.avatar;
      this.nickname = res.user.nickName;
    });
  },
};
</script>

<style scoped>
.navd {
  width: 100%;
  height: 119px;
  background: #171c1f;
  position: relative;
  margin-bottom: 12px;
}
.navd h3 {
  font-size: 28px;
  font-family: PingFang SC-Bold, PingFang SC;
  font-weight: lighter;
  color: #faf7f4;
  position: absolute;
  top: 40px;
  left: 50px;
}
.navd .user .avatar {
  width: 43px;
  height: 43px;
  border-radius: 100%;
  position: absolute;
  top: 39px;
  right: 180px;
  cursor: pointer;
}
.navd .user .name {
  font-size: 18px;
  font-family: PingFang SC-Bold, PingFang SC;
  font-weight: lighter;
  color: #f4f0e9;
  position: absolute;
  top: 48px;
  right: 87px;
}
.navd .user .users {
  width: 15px;
  height: 8px;
  position: absolute;
  top: 57px;
  right: 58px;
  cursor: pointer;
}
.userinfo {
  width: 165px;
  height: 128px;
  background: #ffffff;
  box-shadow: 0px 5px 16px 1px rgba(121, 121, 121, 0.62);
  border-radius: 10px 10px 10px 10px;
  position: absolute;
  top: 107px;
  right: 58px;
  z-index: 999;
}
.userinfos {
  display: none;
}
.userinfo .tops {
  position: absolute;
  top: 20px;
  width: 165px;
  height: 46px;
  background: #f0f0f0;
}
.userinfo .avatars {
  width: 34px;
  height: 34px;
  border-radius: 100%;
  position: absolute;
  top: 6px;
  left: 11px;
  cursor: pointer;
}
.userinfo .names {
  position: absolute;
  top: 11px;
  right: 22px;
  font-size: 18px;
  font-family: PingFang SC-Bold, PingFang SC;
  font-weight: normal;
  color: #232271;
  cursor: pointer;
}
.userinfo .exit {
  position: absolute;
  top: 84px;
  left: 40px;
  font-size: 18px;
  font-family: PingFang SC-Bold, PingFang SC;
  font-weight: normal;
  color: #666666;
  cursor: pointer;
}
</style>