import request from '../utils/request'
export function logins(data) {
    return request ({
        url: '/api/login',
        method: 'POST',
        data
    })
}
export function loginout(data) {
    return request ({
        url: '/api/logout',
        method: 'POST',
        data
    })
}
export function userinfo(data) {
    return request ({
        url: '/ktv-player-manage/PlayerUserList',
        method: 'POST',
        data
    })
}

