import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  //定义了应用状态的数据结构======页面中的data
  state: {
    ktv:'KTV名称',
    lza :0,
    index:0
  },
  //相当于计算属性 return 允许组件从 store 中获取数据
  getters: {
    lza666(state){
      return state.lza +1
    },
    index666(state){
      return state.index
    }
  },
  //是唯一更改 store 中状态的方法，且必须是同步函数
  mutations: {
    suibian(state,value){
      state.lza += value
    },
    suibian2(state,value){
      state.index += value
    }
  },
  //用于提交 mutation，而不是直接变更状态，可以包含任意异步操作//异步调用mutations的方法
  actions: {
    sbaction(context,value){
      context.commit('suibian',value)
    },
    sbaction2(context,value){
      context.commit('suibian2',value)
    }
  },
  //可以将 store 分割成模块 (module)。//每个模块拥有自己的 state、mutation、action、getter、甚至是嵌套子模块
  modules: {
  }
})
