<template>
  <el-tabs
    v-model="tabsValue"
    type="card"
    editable
    @tab-remove="removeTab"
    @tab-click="tabClick"
  >
    <el-tab-pane
      :key="index"
      v-for="(item, index) in tabs"
      :label="item.title"
      :name="item.name"
    >
    </el-tab-pane>
  </el-tabs>
</template>
<script>
export default {
  data() {
    return {
      tabsValue: "/home",
      tabs: [
        {
          title: "首页",
          name: "/home",
          component: "Home",
        },
      ],
      tabIndex: 1,
    };
  },
  watch: {
    $route: {
      handler(newValue) {
        // tab去重处理
        // 判断子组件名和路由元信息中的子组件名相同
        const index = this.tabs.findIndex(
          (item) => item.component === newValue.meta.tabInfo.component
        );
        // 如果tab存在,则只切换当前tab,不添加
        if (index !== -1) {
          this.tabsValue =
            this.tabs[
              index
            ].name;
          return;
        }
        // 路由变化后将当前路由和默认高亮的菜单保持一致,避免页面刷新还是默认的值
        // 添加tab信息到集合列表,添加一个顺序往后排一个
        this.tabs.push({
          title: newValue.meta.tabInfo.title,
          component: newValue.meta.tabInfo.component,
          name: newValue.path,
        });
        // 每次添加后取最后一个的name显示新点击的tab
        this.tabsValue = this.tabs[this.tabs.length - 1].name;
      },
      // 页面首次加载也监听路由的值
      immediate: true,
    },
  },
  methods: {
    tabClick({ name }) {
      console.log(name);
      if (name === this.$route.path) {
        return;
      } else {
        this.$router.push(name);
      }
    },
    removeTab(name) {
      console.log(name);
      let tabs = this.tabs;
      let activeName = this.tabsValue;
      tabs.forEach((tab, index) => {
        if (tab.name === name) {
          const nextTab = tabs[index - 1] || tabs[index + 1];
          // 将下一个tab高亮
          activeName = nextTab.name;
          // 删除掉要删除的目标tab
          this.tabs.splice(index, 1);
        }
      });
      // 路由跳转到删除后的下一个tab,触发路由监听改变左边对应菜单栏的高亮
      this.$router.push(activeName);
    },
  },
  create() {},
  mounted() {},
};
</script>
<style scoped>
::v-deep .el-tabs__new-tab {
  display: none;
}
::v-deep .el-tabs__item.is-active {
  font-size: 16px;
  font-family: PingFang SC-Regular, PingFang SC;
  font-weight: 400;
  color: #f4f0e9;
}
::v-deep .el-tabs--card > .el-tabs__header {
  border: none !important;
}
::v-deep .el-tabs__header {
  margin: 0;
  border: none !important;
}
::v-deep .el-tabs--card > .el-tabs__header .el-tabs__item.is-active {
  border: none !important;
}
::v-deep .el-tabs__nav {
  border: none !important;
}
::v-deep .el-tabs__item {
  font-size: 16px;
  font-family: PingFang SC-Regular, PingFang SC;
  font-weight: 400;
  color: #c7c4c0;
  width: 131px;
  height: 45px;
  background: #171c1f;
  border-radius: 6px 6px 6px 6px;
  padding: 0;
  border: none !important;
  margin-right: 14px;
}
</style>